import React from 'react'

class Contact extends React.Component{
  render () {
    return(
      <article id="contact">
        <h2 className="major">联系我</h2>
        <form method="post" action="https://formspree.io/1005115696@qq.com">
          <div className="fields">
            <div className="field half">
              <label htmlFor="name">昵称</label>
              <input type="text" name="name" id="name"/>
            </div>
            <div className="field half">
              <label htmlFor="email">邮箱</label>
              <input type="text" name="email" id="email"/>
            </div>
            <div className="field">
              <label htmlFor="message">留言</label>
              <textarea name="message" id="message" rows="4"/>
            </div>
          </div>
          <ul className="actions">
            <li><input type="submit" value="提交留言" className="primary"/></li>
            <li><input type="reset" value="重置" style={{width:'7rem'}}/></li>
          </ul>
        </form>
        <ul className="icons">
          <li><a href="#" className="icon brands fa-twitter"><span className="label">Twitter</span></a></li>
          <li><a href="#" className="icon brands fa-github"><span className="label">GitHub</span></a></li>
          <li><a href="#" className="icon brands fa-weibo"><span className="label">Weibo</span></a></li>
        </ul>
        <div className='close' onClick={this.goToHomePage}>Close</div>
      </article>
    );
  }

  goToHomePage = () => {
    this.props.history.push('/');
  }
}

export default Contact;