import React from 'react';
import {NavLink} from 'react-router-dom';
import {inject} from 'mobx-react';
import store from './store.js';
import Content from '../Content';

@inject((props) =>{
  return {
    ...props,
    uiStore: store
  }
})
class Header extends React.Component{
  renderNavLi = (data) => {
    const {uiStore} = this.props;
    return (data || []).map((item, index) =>
      (
        <li className={(index / 2 === 0 ? 'is-middle' : '')} key={index}>
          <NavLink to={`${uiStore.routes[index]}`}>{item}</NavLink>
        </li>
      ));
  }
  render () {
    const {uiStore} = this.props;
    return(
      <header id='header'>
        <div className='logo'>
          <span className="icon fa-gem"></span>
        </div>
        <Content/>
        <nav>
          <ul className={uiStore.liList%2===0 ? 'use-middle' : ''}>
            {this.renderNavLi(uiStore.liList)}
          </ul>
        </nav>
      </header>
    );
  }
}

export default Header;